/*RESET v3*/
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
a,
cite,
img,
sub,
sup,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0;
  font: inherit;
  margin: 0;
  padding: 0;
  font-weight: normal;
  vertical-align: baseline; }

html,
body {
  height: 100%; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

button {
  outline: none; }

ol,
ul {
  margin: 0;
  padding: 0; }

ol,
ul,
li {
  list-style: none; }

i,
em {
  font-style: normal; }

blockquote {
  quotes: none; }

blockquote:before,
blockquote:after {
  content: "";
  content: none; }

a {
  outline: none;
  text-decoration: none;
  text-decoration: none;
  text-decoration: none;
  transition: background-color 0.5s ease 0s, color 0.5s ease 0s, width 0.5s ease 0s, border-width 0.5s ease 0s, border-color 0.5s ease 0s;
  -webkit-transition: background-color 0.5s ease 0s, color 0.5s ease 0s, width 0.5s ease 0s, border-width 0.5s ease 0s, border-color 0.5s ease 0s;
  -moz-transition: background-color 0.5s ease 0s, color 0.5s ease 0s, width 0.5s ease 0s, border-width 0.5s ease 0s, border-color 0.5s ease 0s;
  -ms-transition: background-color 0.5s ease 0s, color 0.5s ease 0s, width 0.5s ease 0s, border-width 0.5s ease 0s, border-color 0.5s ease 0s;
  -o-transition: background-color 0.5s ease 0s, color 0.5s ease 0s, width 0.5s ease 0s, border-width 0.5s ease 0s, border-color 0.5s ease 0s; }

input,
textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  outline: none;
  border: 0 none;
  margin: 0;
  padding: 0; }

select,
option {
  font-family: inherit; }

.noselect {
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none; }

.clear {
  clear: both; }

/*textarea,input,a{*/
/*-webkit-appearance: none;*/
/*}*/
.clearfix:before,
.clearfix:after {
  content: "";
  display: table; }

.clearfix:after {
  clear: both; }

.clearfix {
  *zoom: 1; }

.zc-button {
  background-color: #3378dd;
  color: #ffffff;
  border-radius: 4px;
  border: none;
  padding: 0px 15px;
  height: 30px;
  font-size: 14px;
  cursor: pointer; }
  .zc-button:hover {
    background-color: #3d82e7; }
  .zc-button:focus {
    outline: none; }
  .zc-button[disabled] {
    background-color: #ebebeb; }

.zc-sliver {
  height: 40px;
  position: absolute;
  left: 0px;
  right: 0px;
  padding-left: 20px;
  display: flex;
  align-items: center;
  font-size: 16px;
  background-color: #3378dd;
  color: #ffffff; }

.zc-sliver > .zc-sliver-close {
  position: absolute;
  right: 20px; }

.zc-circle {
  display: inline-block;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  border-style: solid;
  border-width: 1px;
  border-color: #3378dd;
  color: #ffffff;
  background-color: #3378dd; }

.zc-modal {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5); }

.zc-popup-panels-parents {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  z-index: 3;
  padding: 0px;
  margin: 0px; }

.zc-popup-panels {
  position: relative;
  margin: 0px auto;
  padding: 20px 20px;
  top: 50px;
  z-index: 3;
  width: 500px;
  background-color: #ffffff;
  border-radius: 4px; }

.zc-popup-panels-header {
  position: relative;
  border-bottom: solid 1px #efefef;
  height: 40px;
  line-height: 40px; }

.zc-popup-panels-content {
  position: relative;
  overflow-y: auto;
  height: 500px;
  padding-top: 20px;
  padding-bottom: 20px; }

.zc-popup-panels-footer {
  position: relative;
  border-top: solid 1px #efefef;
  height: 40px;
  line-height: 40px; }

.zc-input-parents {
